.main-navigation__menu-btn {
    width: 5rem;
    height: 5rem;
    background: transparent;
    border: skyblue;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-right: 2rem;
    cursor: pointer;
    align-items: center;
  }
  
  .navitems{
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: auto;
    align-items: left;
    font-size: 16px;
    line-height: 1.5;
    color: #485d65;
    font-weight: 400;
    list-style: none;
    position: absolute;

}
  .spacer{
      flex: 1;
  }
 .header__logo a {
      background:#485d65;
      font-size: 2rem;
      text-decoration: none;
      float: left;
      display:flex;
    }
  .main-navigation__menu-btn span {
    display: block;
    width: 3rem;
    height: 2.5px;
    background: white;
  }
  
  .main-navigation__title {
    float: left;
    /* color: white; */
    margin-left: 223px;
  }
  
  .main-navigation__title a {
    font-family: Ne10-simplified,"Helvetica Neue",Helvetica,Roboto,Arial,sans-serif;
    box-sizing: border-box;
    text-decoration: none;
    color: white;
  }
  
  .main-navigation__header-nav {
    display: none;
  }
  
  .main-navigation__drawer-nav {
    height: 100%;
  }
  
  @media (min-width: 768px) {
    .main-navigation__menu-btn {
      display: none;
    }
  
    .main-navigation__header-nav {
      display: block;
    }
    
    .header-background {
      width: 1359px;
      height: 100px;
      display: block;
      position: relative;
      margin: 0 auto;
      background-color: #375967;
    }

    .search{

      float: right;


    }
  }