.main-header {
    width: 100%;
    display: block;
    background: #485d65;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  }
  
  main {
    margin-top: 6rem;
  }
  
  @media (min-width: 768px) {
    .main-header {
      justify-content: space-between;
      box-sizing: border-box;
      font-family: Ne10-simplified,"Helvetica Neue",Helvetica,Roboto,Arial,sans-serif;
    }
  }